import {
	defaultAddress,
	deleteAddress
} from '@/http/address.js';

export default {
	props: {
		addressList: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	data() {
		return {
			seleteDefault: false
		}
	},
	created() {
		this.userId = uni.getStorageSync('userId');
	},
	methods: {
		changeSelete(id) {
			defaultAddress({
				id,
				userId: this.userId
			}).then(res => {
				if (res.data.status === 0) {
					uni.showToast({
						title: '设置成功'
					})
					this.$emit('changeSelete')
				}
			})
		},
		async removeAddress(id) {
			let result = await deleteAddress(id);
			if (result.data !== null && result.data.status === 0) {
				uni.showToast({
					title: '删除成功'
				})
				this.$emit('removeAddress')
			}
		},
		editAddress(id) {
			uni.navigateTo({
				url: `/pages/addAddress/addAddress?id=${id}`
			})
		}
	}
}
